*{
  box-sizing: border-box;
  margin: 0;
  padding:0 ;
}
body{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
p{
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}
.app{
  width: 100%;
  height: 110vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
}
.app:before{
  content: '';
  background: url('back.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 110vh;
  top: 0;
  left: 0;
  z-index: -1;
}
.container{
  max-width: 700px;
  height: 500px;
  margin: auto;
  padding: 0 1rem ;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.top{
  width: 100%;
  margin: 1rem auto;
  /* display: flex; */
  /* justify-content: space-between; */
  
}
.top .clod{
  position: relative;
  right: 0%;
}
