.bottom{
  text-align: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.hmidity {
  width: 200px;
  height: 100px;
  padding: 19px;
  position: relative;
  background-color: rgba(0, 0, 0, 0.8);
}
.wind {
  width: 200px;
  height: 100px;
  padding: 19px;
  background-color: rgba(0, 0, 0, 0.8);
}
.feels {
  width: 200px;
  height: 100px;
  padding: 19px;
  background-color: rgba(0, 0, 0, 0.8);
}
.bold{
  font-weight: 700;
}
.search{
  text-align: center;
  padding: 2rem;
}
::placeholder{
  color: #f8f8f8;
}
input{
  font-size: 1.2rem;
  padding: 0.9rem 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.2);
  color: #f8f8f8;
}
.btn{
  font-size: 1.2rem;
  padding: 0.9rem 1.5rem;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.2);
}
@media (max-width: 600px) {
  .bottom {
    flex-direction: column;
    position: relative;

  }
  .hmidity {
    margin: 10px;
    width: 90%;
    height: 6rem;
  }
  .feels {
    margin: 10px;
    width: 90%;
    height: 6rem;
  }
  .wind {
    margin: 10px;
    width: 90%;
    height: 6rem;
  }
}